import { Injectable } from '@angular/core';
import { Calendar } from '@ionic-native/calendar/ngx';
import { LoggerInteractor } from '@nequi/features/commons/core';

export enum CalendarRecurrence {
  DAILY = 'daily',
  WEEKLY = 'weekly',
  MONTHLY = 'monthly',
  YEARLY = 'yearly',
}

export interface CalendarOptions {
  firstReminderMinutes?: number;
  secondReminderMinutes?: number;
  recurrence?: CalendarRecurrence;
  recurrenceInterval?: number;
  recurrenceWeekstart?: string;
  recurrenceByDay?: number;
  recurrenceByMonthDay?: number;
  recurrenceEndDate?: Date;
  recurrenceCount?: number;
  calendarName?: string;
  calendarId?: number;
  url?: string;
}

export interface CalendarParams {
  title: string;
  location?: string;
  description?: string;
  startDate: Date;
  endDate: Date;
  options?: CalendarOptions;
  notes?: string;
}

interface Calendars {
  displayname: string;
  id: string;
  isPrimary: boolean;
  name: string;
}

interface Events {
  allday: boolean;
  endDate: string;
  id: string;
  location: string;
  message: string;
  startDate: string;
  title: string;
}

const CALENDAR_NAME = 'Nequi';
const CALENDAR_COLOR = '#210049';

@Injectable({ providedIn: 'root' })
export class CalendarPlugin {
  constructor(private calendar: Calendar, private loggerInteractor: LoggerInteractor) {
    this.createCalendar();
  }

  public async getCalendarOptions(): Promise<CalendarOptions> {
    return this.calendar.getCalendarOptions() as CalendarOptions;
  }

  public async deleteEvents(params: CalendarParams) {
    const { title, location, notes, startDate, endDate } = params;

    return this.calendar.deleteEvent(title, location, notes, startDate, endDate);
  }

  public async createEvent(params: CalendarParams) {
    const { title, location, description, startDate, endDate, options } = params;

    const calendar = await this.getCalendars();

    options.calendarId = Number(calendar.id);
    options.calendarName = CALENDAR_NAME;

    return this.calendar.createEventWithOptions(
      title,
      location,
      description,
      startDate,
      endDate,
      options
    );
  }

  public async deleteEvent(params: CalendarParams) {
    const { title, location, notes, startDate, endDate } = params;

    const calendar = await this.getCalendars();

    const options: CalendarOptions = {
      calendarId: Number(calendar.id),
      calendarName: CALENDAR_NAME,
    };

    const events: Events[] = await this.calendar.findEventWithOptions(
      title,
      location,
      notes,
      startDate,
      endDate,
      options
    );

    if (events) {
      await this.calendar.deleteEventById(events[0].id);
    }
  }

  private async createCalendar() {
    try {
      await this.calendar.createCalendar({
        calendarName: CALENDAR_NAME,
        calendarColor: CALENDAR_COLOR,
      });
    } catch (error) {
      this.loggerInteractor.error(`[calendar-plugin][createCalendar] ${JSON.stringify(error)}`);
    }
  }

  private async getCalendars(): Promise<Calendars> {
    const calendars: Calendars[] = await this.calendar.listCalendars();

    return calendars.find((e) => e.name === CALENDAR_NAME);
  }
}
