import { Injectable } from '@angular/core';
import { cordova, IonicNativePlugin } from '@ionic-native/core';

interface Headers {
  name: string;
  value: string;
}

interface WebViewParams {
  url: string;
  title: string;
  loadLabel: string;
  headers?: Array<Headers>;
}

@Injectable({ providedIn: 'root' })
export class NequiWebView extends IonicNativePlugin {
  public static repo =
    'https://integracion.bancadigital.com.co/bcol-front/cordova-plugins/cordova-nequi-plugin-web-view';

  public static pluginName = 'NequiWebView';
  public static plugin = 'cordova-nequi-plugin-web-view';
  public static pluginRef = 'cordova.plugins.nequiWebView';
  public static platforms = ['Android', 'iOS'];
  public static install = 'ionic cordova plugin add cordova-nequi-plugin-web-view';

  public async launchWebView(params: WebViewParams): Promise<any> {
    return new Promise((resolve, reject) => {
      cordova(this, 'launchWebView', {}, [
        params.url,
        params.title,
        params.loadLabel,
        params.headers || [],
        resolve,
        (error) => {
          reject(error);
        },
      ]);
    });
  }
}
